.loading-audio {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;

  &.show {
    opacity: 1;
  }

  .loading-dot {
    display: inline-block;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin: 0 5px;
    background-color: #eee;
    animation: lethargic-bounce 1.5s linear infinite;

    &:nth-child(2) {
      animation-delay: -1.2s;
    }

    &:nth-child(3) {
      animation-delay: -0.9s;
    }
  }
}

@keyframes lethargic-bounce {
  0%,
  75%,
  100% {
    transform: initial;
  }

  30% {
    transform: translateY(10px);
  }
}
